import React, { Component } from "react";
import { Route, HashRouter } from "react-router-dom";
import { connect } from "react-redux";
import L from "react-loadable";

import api from "./apis/LaunchLibrary";

class MyRouter extends Component {
  async componentDidMount() {
    const launches = await api.launches();
    this.props.addLaunches(launches);
  }

  render() {
    return (
      <HashRouter ref={r => (this.router = r)}>
        <div>
          {/* <div className="header">
            <Link to="/">
              <img
                alt="logo"
                src={logo}
                height="24"
                style={{ verticalAlign: "middle" }}
              />{" "}
              Liftoff
            </Link>
          </div> */}
          <Route exact path="/" component={AsyncLaunchesPage} />
          <Route
            path="/launch/:id"
            component={AsyncLaunchPage}
            onUpdate={() => window.scrollTo(0, 0)}
          />
          <div id="footer">
            API provided by{" "}
            <a href="https://launchlibrary.net/">LaunchLibrary</a>
          </div>
        </div>
      </HashRouter>
    );
  }
}

const AsyncLaunchPage = L({
  loader: () =>
    import(/* webpackChunkName:"LaunchPage" */ "./Launch/LaunchPage"),
  loading: () => <div />
});

const AsyncLaunchesPage = L({
  loader: () =>
    import(/* webpackChunkName:"LaunchesPage" */ "./Launches/LaunchesPage"),
  loading: () => <div />
});

const mapStateToProps = ({ data: { launches } }) => ({
  launches
});

const mapDispatchToProps = dispatch => ({
  addLaunches: value => dispatch({ type: "ADD_LAUNCHES", value })
});

export default connect(mapStateToProps, mapDispatchToProps)(MyRouter);



